<template>
  <div id="app"
       v-loading.fullscreen.lock="fullscreenLoading"
       element-loading-text="拼命计算中">
    <el-row>
      <el-col :span="10">
        <div class="select-file">
          <input type="file"
                 id="file"
                 placeholder="请选择文件"
                 style="margin-right:10px">
          <el-button type="primary"
                     size="small"
                     @click="handleSubmit">(～￣▽￣)～确定</el-button>
        </div>
      </el-col>
      <el-col :span="7"
              v-if="dataList">
        <div @keyup.enter="handleSearch">
          <el-input placeholder="按姓名或编号搜索(￣▽￣)~*"
                    v-model="searchInput">
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="handleSearch">搜索</el-button>
          </el-input>
        </div>
      </el-col>
      <el-col :span="7"
              v-if="dataList">
        <el-select v-model="searchType"
                   clearable
                   placeholder="筛选考勤类型"
                   @change="handleTypeSelectChange">
          <el-option label="正常"
                     value="normal"></el-option>
          <el-option label="异常"
                     value="abnormal"></el-option>
          <el-option label="迟到"
                     value="be_late"></el-option>
          <el-option label="早退"
                     value="leave_early"></el-option>
          <el-option label="加班"
                     value="work_overtime"></el-option>
          <el-option label="缺卡"
                     value="miss_card"></el-option>
          <el-option label="无记录"
                     value="no_data"></el-option>
        </el-select>
        <div>
          &nbsp;
          <span v-if="searchType">搜索到记录数：{{typeSearchCount/2}}条</span>
        </div>
      </el-col>
    </el-row>
    <div class="table-wrapper">
      <el-table v-if="dataList"
                :data="dataList"
                style="width: 100%"
                height="90vh">
        <el-table-column type="index"
                         width="50"
                         fixed></el-table-column>
        <el-table-column prop="id"
                         label="编号"
                         min-width="100px"
                         fixed></el-table-column>
        <el-table-column prop="name"
                         label="姓名"
                         min-width="100px"
                         fixed></el-table-column>
        <el-table-column label="日期">
          <el-table-column label="星期"></el-table-column>
        </el-table-column>
        <el-table-column v-for="(item, index) in monthDate"
                         :key="index"
                         :label="String(item.day)">
          <el-table-column :label="item.weekCn.replace('星期','')">
            <template slot-scope="scope">
              <div class="today-status"
                   @click="handleShowDetail(scope.row.data[index].data)"
                   v-html="getRecordItem(scope.row.data[index])"></div>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="明细数据"
               :visible.sync="dialogVisible">
      <div class="dialog-content"
           v-if="detailData">
        <h3>人员编号：{{detailData[0]['人员编号']}}</h3>
        <h3>姓名：{{detailData[0]['姓名']}}</h3>
        <h4>考勤时间：</h4>
        <ul style="list-style:none;margin:0;padding:0">
          <li v-for="(item, index) in detailData"
              :key="index">{{item['考勤时间']}}</li>
        </ul>
      </div>
      <p v-else>无打卡数据</p>
    </el-dialog>
  </div>
</template>

<script>
// const XLSX = require('xlsx')
let $ = document.querySelector.bind(document)
// 获取一个月有多少天
function getDaysInOneMonth(year, month) {
  month = parseInt(month, 10)
  let d = new Date(year, month, 0)
  return d.getDate()
}

// 获取一个月中每天的日期和星期
function getMonthData(year, month) {
  let dayCount = getDaysInOneMonth(year, month)
  let date = new Date(year, month - 1, 1)
  const weekMap = [
    '星期日',
    '星期一',
    '星期二',
    '星期三',
    '星期四',
    '星期五',
    '星期六'
  ]
  let ret = []
  for (let index = 1; index <= dayCount; index++) {
    date.setDate(index)
    ret.push({
      week: date.getDay(),
      weekCn: weekMap[date.getDay()],
      day: index
    })
  }
  return ret
}

const statusMap = {
  no_data: 'status-red',
  miss_card: 'status-yellow',
  be_late: 'status-purple',
  leave_early: 'status-purple',
  be_late_leave_early: 'status-purple',
  work_overtime: 'status-blue',
  normal: ''
}

let tableData = null
let tmpTypeSearchCount = 0
// let counter = 0
export default {
  name: 'landing-page',
  data() {
    return {
      searchInput: '',
      searchVal: '',
      searchType: '',
      monthDate: null,
      dataList: null,
      detailData: null,
      dialogVisible: false,
      fullscreenLoading: false,
      typeSearchCount: 0
    }
  },
  methods: {
    handleSearch() {
      this.searchVal = this.searchInput
      this.dataList = tableData.filter(
        data =>
          !this.searchVal ||
          String(data.name).match(this.searchVal) ||
          String(data.id).match(this.searchVal)
      )
      tmpTypeSearchCount = 0
      this.$nextTick(() => {
        setTimeout(() => {
          this.typeSearchCount = tmpTypeSearchCount
        }, 100)
      })
    },
    handleTypeSelectChange() {
      tmpTypeSearchCount = 0
      this.$nextTick(() => {
        setTimeout(() => {
          this.typeSearchCount = tmpTypeSearchCount
        }, 100)
      })
    },
    getRecordItem(item) {
      // console.log('====item====', item, counter++)
      if (
        !this.searchType ||
        (this.searchType == 'abnormal' &&
          item.status != 'normal' &&
          item.status != 'work_overtime') ||
        (this.searchType != 'abnormal' && this.searchType.match(item.status))
      ) {
        tmpTypeSearchCount++
        return `<span class="${statusMap[item.status]}">${item.statusCn}</span>`
      }
      return ''
    },
    dateFilter(val) {
      if (val && val.join) {
        return val.map(item => item['考勤时间']).join('<br>')
      }
      return ''
    },
    handleShowDetail(data) {
      this.detailData = data
      this.dialogVisible = true
    },
    handleSubmit() {
      this.fullscreenLoading = true
      setTimeout(() => {
        try {
          if (!$('#file').files[0]) {
            this.$message({
              message: '要先选择xlsx文件',
              type: 'warning'
            })
            return
          }
          let files = $('#file').files
          var fileReader = new FileReader()
          fileReader.onload = ev => {
            try {
              var data = ev.target.result

              var workbook = XLSX.read(data, {
                type: 'binary'
              })
              // 以二进制流方式读取得到整份excel表格对象

              var persons = [] // 存储获取到的数据
            } catch (e) {
              console.log('文件类型不正确')
              return
            }

            // 表格的表格范围，可用于判断表头是否数量是否正确
            // var fromTo = ''
            // 遍历每张表读取
            for (var sheet in workbook.Sheets) {
              if (workbook.Sheets.hasOwnProperty(sheet)) {
                // fromTo = workbook.Sheets[sheet]['!ref']
                persons = persons.concat(
                  XLSX.utils.sheet_to_json(workbook.Sheets[sheet])
                )
              }
            }

            console.log('====time====', persons[0]['考勤时间'])
            let date = new Date(persons[0]['考勤时间'])
            let monthDate = getMonthData(
              date.getFullYear(),
              date.getMonth() + 1
            )
            console.log('====monthDate====', monthDate)
            this.monthDate = monthDate
            let group = {}
            persons.forEach(item => {
              item.date = new Date(item['考勤时间'])
              let personID = item['人员编号'] || item['员工号']
              if (!group[personID]) {
                group[personID] = []
              }
              group[personID].push(item)
            })
            let ret = []
            Object.keys(group).forEach(personKey => {
              let person = group[personKey]
              let result = {
                id: personKey,
                name: group[personKey][0]['姓名']
              }
              result.data = monthDate.map(dayItem => {
                let target = person.filter(personDay => {
                  return personDay.date.getDate() == dayItem.day
                })
                let itemData
                if (!target || !target.length) {
                  if (dayItem.week == 0 || dayItem.week == 6) {
                    itemData = {
                      status: 'normal_weekend',
                      statusCn: ''
                    }
                  } else {
                    itemData = {
                      status: 'no_data',
                      statusCn: '无记录'
                    }
                  }
                } else {
                  target.sort((a, b) => {
                    return a.date - b.date
                  })
                  if (dayItem.week == 0 || dayItem.week == 6) {
                    itemData = {
                      status: 'work_overtime',
                      statusCn: '加班',
                      data: [...target]
                    }
                  } else {
                    let beLate = false
                    let leaveEarly = false
                    let lack = false
                    if (target.length < 2) {
                      lack = true
                    }
                    if (
                      target[0].date.getHours() > 8 ||
                      (target[0].date.getHours() == 8 &&
                        target[0].date.getMinutes() > 30)
                    ) {
                      beLate = true
                    }
                    if (target[target.length - 1].date.getHours < 18) {
                      leaveEarly = true
                    }
                    if (lack) {
                      itemData = {
                        status: 'miss_card',
                        statusCn: '缺卡',
                        data: [...target]
                      }
                    } else if (beLate && leaveEarly) {
                      itemData = {
                        status: 'be_late_leave_early',
                        statusCn: '迟到+早退',
                        data: [...target]
                      }
                    } else if (beLate) {
                      itemData = {
                        status: 'be_late',
                        statusCn: '迟到',
                        data: [...target]
                      }
                    } else if (leaveEarly) {
                      itemData = {
                        status: 'leave_early',
                        statusCn: '早退',
                        data: [...target]
                      }
                    } else {
                      itemData = {
                        status: 'normal',
                        statusCn: '正常',
                        data: [...target]
                      }
                    }
                  }
                }
                itemData.week = dayItem.week
                return itemData
              })
              ret.push(Object.freeze(result))
            })
            console.log('====ret====', ret)
            tableData = Object.freeze(ret)
            this.$nextTick(() => {
              this.dataList = tableData
              setTimeout(() => {
                this.fullscreenLoading = false
              }, 500)
            })
          }

          // 以二进制方式打开文件
          fileReader.readAsBinaryString(files[0])
        } catch (err) {
          console.log('====err====', err)
          alert('啊呦，出错了，检查一下文件是否正确')
        }
      }, 200)
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.table-wrapper {
  max-width: 100vw;
  overflow: auto;
}

.today-status {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}

@mixin status($size: 7px) {
  display: flex;
  align-items: center;
  &:before {
    content: '';
    width: $size;
    height: $size;
    display: inline-block;
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 7px;
  }
}

.status-red {
  @include status();
  &:before {
    background-color: #f56c6c;
  }
}

.status-purple {
  @include status();
  &:before {
    background-color: #9b59b6;
  }
}

.status-yellow {
  @include status();
  &:before {
    background-color: #e6a23c;
  }
}

.status-blue {
  @include status();
  &:before {
    background-color: #409eff;
  }
}

.status-green {
  @include status();
  &:before {
    background-color: #67c23a;
  }
}

.status-gray {
  @include status();
  &:before {
    background-color: #909399;
  }
}
</style>
