import Vue from 'vue'
import App from './App.vue'
import {
  Row,
  Col,
  Table,
  TableColumn,
  Dialog,
  Button,
  Loading,
  Input,
  Select,
  Option
} from 'element-ui'

Vue.use(Row)
Vue.use(Col)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Dialog)
Vue.use(Button)
Vue.use(Loading.directive)
Vue.use(Input)
Vue.use(Select)
Vue.use(Option)

Vue.config.productionTip = false

new Vue({
  render: h => h(App)
}).$mount('#app')
